@-webkit-keyframes anime-one {
  0% {
    height: 40px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: -webkit-fit-content;
    height: fit-content;
  }
}

@keyframes anime-one {
  0% {
    height: 40px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}

